.center{
  margin: 0 auto;
  display: block;
}

.hover-overlay{
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(61, 61, 61, 0.2);
  z-index: 10;
  transition: all ease-in-out 300ms;
  opacity: 0;
  ul{
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 50px;
    list-style-type: none;
    margin-top: 100px;
    transition: all ease-in-out 300ms;

    li{
      color:#ffffff;
      font-size: 30px;
    }
  }
  &:hover{
   opacity: 1;
  }
}

.tile{
  position: relative;
  background: #cccccc;
  border: 1px solid #ffffff;
  text-align: center;
  transition: all ease-in-out 300ms;
  cursor:pointer;



  &.bodhan{
    background: #fdfaf5;
  }

  &.lnt{
    background: #d5e5ea;
  }

  &.covervidz{
    background: #9d89b3;
  }

  &.bus{
    img{
      margin-top: 50px;
      margin-bottom: 50px;
    }
  }

  img{
    margin-top: 100px;
    margin-bottom: 100px;
    transition: all ease-in-out 300ms;

  }


  &:hover{
    img{
      transform: scale(0.8);
    }
  }
  h3{
    color:rgba(0,0,0,0);
    transition: all ease-in-out 300ms;
  }
}

.navigation{
  li{
    a{
      color: #444;
    }
  }
}

.header{
  background: url(../../img/header-bg-home.jpg);
  background-size: cover;
  background-attachment: fixed;
  min-height: 500px;
  padding-top: 300px;
  h1{
    font-size: 65px;
    margin-bottom: 10px;
    margin-top: 10px;
    color:#ffffff;
  }

  p{
    font-size: 17px;
    color:#ffffff;
  }

  img.logo{
    width:70px;
  }
}

.what-i-use{
  background: #d9d9da;
}

.footer{
  height: 150px;
  padding: 50px;
}


